












































































































































































































import { Component, Vue } from "vue-property-decorator";
import TextField from "../../components/common/TextField.vue";
import Api from "../../Api";

@Component({
  components: {
    TextField,
  },
})
export default class ProfilePage extends Vue {
  loggedUser = this.$store.getters["login/user"];
  accessToken = this.$store.getters["login/token"];
  uneditable = true;
  isPasswordBtnLoading=false;
  PasswordRestMsg="";
  userDetails = {
    first_name: "",
    last_name: "",
    emai: "",
    password: "",
    avatar: "",
    title: "",
    description: "",
  };
  get readOnly() {
    return this.uneditable;
  }

  test() {
    alert("hi");
  }
  created() {
    if (this.loggedUser != null || this.loggedUser != undefined) {
      this.userDetails = this.loggedUser;
      this.userDetails.avatar = Api.LocalhostImages + this.userDetails.avatar;
    }
  }

  editFields() {
    this.uneditable = !this.uneditable;
  }

  async updateUser() {
    const user = this.loggedUser;
    const userResponse = await fetch(
      Api.Authenticateion.UpdateUser.replace("{id}", user.id),
      {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.accessToken,
        },
        body: JSON.stringify({
          first_name: user.first_name,
          last_name: user.last_name,
          email: user.email,
          title: user.title,
        }),
      }
    );
    let userResponseData = await userResponse.json();
    // if (userResponse.ok){
    userResponseData = userResponseData.data;
    localStorage.setItem("user", JSON.stringify(userResponseData));
    this.userDetails = this.loggedUser;
    this.editFields();
    // }
  }

  async requestPasswordLink() {
    const user = this.loggedUser;
    this.isPasswordBtnLoading=true;
    const userResponse = await fetch(Api.Authenticateion.requestPassword, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.accessToken,
      },
      body: JSON.stringify({
        email: user.email,
      }),
    });
    if (userResponse.ok) {
       this.isPasswordBtnLoading=false;
       this.PasswordRestMsg="تم أرسال رابط اعادة ضبط كلمة السر الى بريدك الالكتروني "
      alert("email sent successfully");
    }
  }
}
